.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1;
}

.expense-item__description img {
  width: 30vw;
  height: 30vw;
  max-width: 30vw;
  max-height: 30vh;
  border-radius: 50%;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #40005d;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}

.expense-item button {
  margin-left: 1rem;
  cursor: pointer;
  border: 1px solid #40005d;
  background-color: #40005d;
  border-radius: 10px;
  padding: 1rem 1rem;
  color: white;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }


  .expense-item__description img {
    width: 10vw;
    height: 20vw;
    max-width: 10vw;
    max-height: 20vh;
    border-radius: 50%;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}