@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}


.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.expense-filters {
  padding: 0 1rem;
  color: white;
}
.expense-filter {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  margin: 1rem 0;
}

.expense-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expense-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
  margin-right: 1rem;
}

.expense-filter button {
  font: inherit;
  padding: 0.5rem;
  font-weight: bold;
  border-radius: 6px;
}


.expense-filter select > option {
  background-color: black;
  color: white;
}
.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  border: 1px solid #313131;
  background-color: #c3b4f3;
  height: 100%;
  border-radius: 12px;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}
.chart {
  padding: 1rem;
  border-radius: 12px;
  background-color: #f8dfff;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 10rem;
}
.expense-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color: #2a2a2a;
  color: white;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year {
  font-size: 0.75rem;
}

.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}

.expense-item__description img {
  width: 30vw;
  height: 30vw;
  max-width: 30vw;
  max-height: 30vh;
  border-radius: 50%;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #40005d;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}

.expense-item button {
  margin-left: 1rem;
  cursor: pointer;
  border: 1px solid #40005d;
  background-color: #40005d;
  border-radius: 10px;
  padding: 1rem 1rem;
  color: white;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }


  .expense-item__description img {
    width: 10vw;
    height: 20vw;
    max-width: 10vw;
    max-height: 20vh;
    border-radius: 50%;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
.expenses p {
  text-align: center;
  color: white;
  text-weight: bold;
}
.expenses {
    padding: 1rem;
    background-color: rgb(31, 31, 31);
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
  }
.expense {
  padding: 1rem;
  background-color: #a892ee;
  width: 50rem;
  margin: 2rem auto;
  max-width: 95%;
  text-align: center;
}

.expense__controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.expense__control span {
  display: block;
  color: rgb(214, 28, 28);
}

.expense label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.expense input {
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 15rem;
  max-width: 100%;
}

.expense__input_error {
  border:2px solid rgb(231, 114, 18) !important;
}

.expense .expense__image {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.expense .expense__image label {
  border: 1px solid #8013b3;
  background-color: #8013b3;
  display: inline-block;
  border-radius:12px;
  color: white;
  padding: 0.5rem;
  cursor: pointer;
}

.expense .expense__image label:hover,
.expense .expense__image label:active {
  background-color: #510674;
  border-color: #510674;
}

.expense .expense__image input {
  display: none;
}

.expense .expense__actions button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;
}

.expense .expense__actions button:hover,
.expense .expense__actions button:active {
  background-color: #510674;
  border-color: #510674;
}
